// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import VueRouter from 'vue-router'
import AboutBona from './components/aboutBona.vue'
import Timeline from './components/timeline.vue'
import Videos from './components/videos.vue'
import AboutUs from './components/aboutUs.vue'
import Links from './components/links.vue'
import UpdateLog from './components/updateLog.vue'
import SNS from './components/sns.vue'
import Articles from './components/articles.vue'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-default/index.css'

Vue.use(VueRouter)
Vue.use(ElementUI)

const routes = [
  { path: '/timeline', name: 'Timeline', component: Timeline },
  { path: '/videos', name: 'Videos', component: Videos },
  { path: '/aboutBona', name: 'AboutBona', component: AboutBona },
  { path: '/aboutUs', name: 'AboutUs', component: AboutUs },
  { path: '/links', name: 'Links', component: Links },
  { path: '/updateLog', name: 'UpdateLog', component: UpdateLog },
  { path: '/sns', name: 'SNS', component: SNS },
  { path: '/articles/:id', name: 'Articles', component: Articles },
  { path: '/', redirect: '/timeline' }
]

const router = new VueRouter({
  routes
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})



