<template>
  <div id="app">
    <img class="mobile-icon" src="static/menu.png" @click="show = !show">
    <transition name="sidebar">
      <div class="mobile-nav-div" v-if="show">
        <nav @click="show = !show" class="mobile-nav" v-if="show">
          <Navigation></Navigation>
        </nav>
      </div>
    </transition>
    <div id="banner" @click="show = false">
      <img src="../static/banner.png">
    </div>
    <div id="main" @click="show = false">
      <div id="nav">
        <Navigation></Navigation>
      </div>
      <div id="timeline">
        <transition name="fade">
          <keep-alive><router-view></router-view></keep-alive>
        </transition>
      </div>
    </div>
  </div>
</template>

<script>
import Navigation from './components/nav.vue'

export default {
  name: 'app',
  data () {
    return {
      show: false,
      isActive: false
    }
  },
  components: {
    Navigation
  }
}
</script>

<style lang="scss">
  #banner {
    width: 100%;
    height: 240px;
    background-color: #9addda;
    text-align: center;
    position: fixed;
    z-index: 2;
    img {
      position: absolute;
      margin-left: -480px;
      height: 240px;
    }
  }
  #main {
    display: -moz-box;
    display: -webkit-box;
    display: box;
    width: 100%;
    height: 100%;
    padding-top: 240px;
  }
  #nav {
    position: fixed;
    height: 100%;
    width: 200px;
    background-color: #324157;
    z-index: 1;
  }
  #timeline {
    padding-left: 200px;
    .about {
      margin-left: 20px;
    }
  }
  .el-carousel {
    overflow: hidden;
    position: relative;
  }
  .mobile-icon {
    display: none;
    position: fixed;
    z-index: 10;
    width: 100px;
    height: 100px;
  }
  .mobile-nav-div {
    position: fixed;
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    z-index: 4;

  }
  .mobile-nav {
    position: fixed;
    padding: 40px;
    padding-top: 200px;
    display: block;
    position: relative;
    -moz-box-flex: 1;
    -webkit-box-flex: 1;
    box-flex: 1;
    background-color: #324157;
    height: 100%;
    width: 300px;
  }
  .mobile-nav ul {
    margin: 0;
    padding: 0;
  }
  .mobile-nav li {
    margin: 0 5px 10px 0;
    list-style: none;
    font-size: 2em;
  }
  .mobile-nav a {
    padding: 3px 12px;
    text-decoration: none;
    color: #999;
    line-height: 100%;
  }
  .mobile-nav a:hover {
    color: #000;
  }
  .sidebar-enter-active, .sidebar-leave-active {
    -webkit-transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
    transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }
  .sidebar-enter, .sidebar-leave-active {
    -webkit-transform: translateX(-200px);
    transform: translateX(-200px);
  }
  .fade-enter-active {
    -webkit-transition: opacity .5s cubic-bezier(1.0, 0.5, 0.8, 1.0);
    transition: opacity .5s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }
  .fade-enter, .fade-leave-active {
    opacity: 0
  }
  @media screen and (min-device-width: 600px) {
    .mobile-nav-div {
      -webkit-transition: all .1s cubic-bezier(1.0, 0.5, 0.8, 1.0);
      transition: all .1s cubic-bezier(1.0, 0.5, 0.8, 1.0);
      -webkit-transform: translateX(-210px);
      transform: translateX(-210px);
    }
  }
  @media screen and (max-device-width: 600px) {
    .mobile-icon {
      display: block;
    }
    .mobile-nav-div {
      transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
      a {
        font-size: 1em;
      }
    }
    #nav {
      display: none;
    }
    #timeline {
      margin-left: 0;
    }
  }
</style>



