<template>
	<div class="sns">
    <el-row :gutter="20" class="pickers">
      <el-col class="date-selector">
        <el-date-picker
          v-model="monthValue"
          type="month"
          placeholder="选择时间"
          :picker-options="disableDays"
          @change="changeTime">
        </el-date-picker>
      </el-col>
    </el-row>
	</div>
</template>

<script>
export default {
  data () {
    return {
      monthValue: '',
      disableDays: {
        disabledDate (time) {
          return time.getTime() > Date.now() || time.getTime() < new Date('2016/1/31 23:59:59').getTime()
        }
      }
    }
  }
}
</script>

<style scoped lang="scss">
  .sns {
    margin-left: 20px;
    margin-top: 20px;
  }
</style>



